@import url("https://fonts.googleapis.com/css?family=Lato:400,700");
.menu {
  display: flex; }
  .menu__list {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    width: 100%; }
    .menu__list_item {
      color: #777;
      font-family: "Lato", sans-serif;
      font-size: 13px;
      line-height: 22px;
      list-style: none;
      margin: 0 15px;
      position: relative; }
      .menu__list_item:before {
        background: #7cad42;
        bottom: 0;
        content: '';
        height: 3px;
        left: 0;
        position: absolute;
        right: 100%;
        transition: right 0.3s ease-out; }
      .menu__list_item:hover, .menu__list_item:focus, .menu__list_item:active {
        color: #000;
        cursor: pointer; }
        .menu__list_item:hover:before, .menu__list_item:focus:before, .menu__list_item:active:before {
          right: 40%; }



